@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Lexend:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;1,400;1,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lexend:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;1,400;1,500&display=swap");
* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  display: block;
}

@font-face {
  font-family: "omega";
  src: url("../../src/fonts/Omega/BROmega-Medium.ttf") format("trueType");
  font-display: swap;
}
h1 {
  font-size: var(--font-size-h1);
}

h2 {
  font-size: var(--font-size-h2);
}

h3 {
  font-size: var(--font-size-h3);
}

h4 {
  font-size: var(--font-size-h4);
}

h5 {
  font-size: var(--font-size-h5);
}

h6 {
  font-size: var(--font-size-h6);
}

:root {
  --site-width-largest: 100rem + 2em;
  --site-width-large: 81.25em + 2em;
  --site-width-medium: 61.875em + 2em;
  --site-width-mobile: 35em + 2em;
  --row-max-width: var(--site-width-large);
}

.row {
  max-width: var(--site-width-largest);
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 81.25em) {
  .row {
    width: min(var(--site-width-large), var(--site-width-largest));
  }
}
@media (max-width: 81.25em) {
  .row {
    width: min(var(--site-width-medium), var(--row-max-width));
  }
}
@media (max-width: 61.875em) {
  .row {
    width: min(var(--site-width-mobile), var(--row-max-width));
  }
}
@media (max-width: 35em) {
  .row {
    width: calc(100% - 2rem);
  }
}

body {
  width: 100%;
  height: var(--doc-height);
}

:root {
  --primary: #000;
  --secondary: #fff;
  --primary-bg: transparent;
  --secondary-bg: #f6f4f0;
  --section-border: #e7e7e7;
  --header-border: #f1f1f1;
  --footer-text: #b8b8b8;
  --social-bg: #000;
  --social-icon: #fefefe;
  --about-subtitle: #484642;
  --about-link: #c7c4c2;
  --related-resource: #202020;
  --related-resource-border: #c0c0c0;
  --image-cover: rgba(0, 0, 0, 0.5);
  --paragraph-text: #999999;
  --link: #979797;
  --link-border: #505050;
  --sub-title: #979594;
  --bio: #83817e;
  --read-link: #343130;
  --max-width: 1600px;
  --site-width-large: 90rem;
  --site-width-medium: 60rem;
  --site-width-small: 48rem;
  --site-width-mobile: 40rem;
  --small: 400;
  --middle: 500;
  --semi-bold: 600;
  --bold: 700;
  --lexend: "Lexend", sans-serif;
  --lora: "Lora", serif;
  --font-size-h1: 3em;
  --font-size-h2: 2.75em;
  --font-size-h3: 2.5em;
  --font-size-h4: 2.25em;
  --font-size-h5: 1.8em;
  --font-size-h6: 1.75em;
}

.section {
  background-color: var(--primary-bg);
  color: var(--primary);
}
.section__dark {
  background-color: var(--secondary-bg);
}

.page-header {
  max-width: 1900px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 0px;
  justify-content: center;
  align-items: flex-start;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 2;
  font-family: var(--lexend);
  background-color: var(--primary-bg);
  transition: background-color 0.3s ease-out, color 0.3s ease-out;
  color: var(--primary);
  border-bottom: 1px solid var(--header-border);
}
.page-header.scroll {
  background-color: rgba(0, 0, 0, 0.8);
  color: var(--secondary);
  border-bottom: none;
}
.page-header.scroll .desktop-header {
  padding: 20px 0px 30px 0px;
}
.page-header .desktop-header {
  display: flex;
  flex-direction: row;
  gap: 10px;
  justify-content: unset;
  align-items: flex-end;
  width: 95%;
  margin: 0 auto;
  max-height: 15%;
  padding: 30px 0px 40px 0px;
}
@media only screen and (max-width: 990px) {
  .page-header .desktop-header {
    padding: 20px 0px;
    justify-content: space-between;
    width: 90%;
  }
}
@media only screen and (max-width: 560px) {
  .page-header .desktop-header {
    width: 95%;
  }
}
.page-header .desktop-header .page-logo {
  display: flex;
  flex-direction: row;
  gap: 2px;
  justify-content: flex-start;
  align-items: flex-end;
  width: 25%;
}
@media only screen and (max-width: 990px) {
  .page-header .desktop-header .page-logo {
    width: unset;
  }
}
.page-header .desktop-header .page-logo .page-logo-text {
  font-size: 1.8em;
  font-weight: var(--bold);
}
.page-header .desktop-header .mobile-menu-open-icon {
  display: none;
}
@media only screen and (max-width: 990px) {
  .page-header .desktop-header .mobile-menu-open-icon {
    display: block;
  }
}
.page-header .desktop-header .mobile-menu-open-icon.hide {
  display: none;
}
.page-header .desktop-header .mobile-menu-open-icon .fa-bars {
  margin-bottom: 5px;
  font-size: 1.8em;
}
.page-header .desktop-header .header-navigation {
  display: flex;
  flex-direction: row;
  gap: 0px;
  justify-content: flex-start;
  align-items: center;
  width: 70%;
}
@media only screen and (max-width: 1120px) {
  .page-header .desktop-header .header-navigation {
    justify-content: center;
  }
}
@media only screen and (max-width: 990px) {
  .page-header .desktop-header .header-navigation {
    display: none;
  }
}
.page-header .desktop-header .header-navigation .navigation-list {
  width: 70%;
  display: flex;
  flex-direction: row;
  gap: 0px;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 1300px) {
  .page-header .desktop-header .header-navigation .navigation-list {
    width: 90%;
    justify-content: space-around;
  }
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .navigation-item-link {
  font-size: 1.1em;
  padding: 5px;
  cursor: pointer;
}
@media only screen and (max-width: 1120px) {
  .page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .navigation-item-link {
    font-size: 14px;
  }
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .dropdown-item-link {
  display: flex;
  flex-direction: row;
  gap: 4px;
  justify-content: center;
  align-items: center;
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .dropdown-item-link .fa-angle-down {
  font-size: 0.6em;
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .list-item-dropdown {
  position: absolute;
  top: 35px;
  left: 0px;
  visibility: hidden;
  opacity: 0;
  padding: 10px;
  width: 240px;
  transition: width 0.3s ease-out;
  z-index: 1;
}
@media only screen and (max-width: 1120px) {
  .page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .list-item-dropdown {
    width: 190px;
  }
}
@media only screen and (min-width: 1900px) {
  .page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .list-item-dropdown {
    width: 280px;
  }
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .list-item-dropdown .dropdown-list {
  display: flex;
  flex-direction: column;
  gap: 0px;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary);
  box-shadow: 0px 0px 30px rgba(127, 137, 161, 0.25);
  border-radius: 5px;
  list-style: none;
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .list-item-dropdown .dropdown-list .dropdown-list-item {
  width: 100%;
  padding: 20px 15px;
  text-align: flex-start;
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .list-item-dropdown .dropdown-list .dropdown-list-item .dropdown-list-link {
  width: 100%;
  cursor: pointer;
  opacity: 0.6;
  font-weight: var(--middle);
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item .list-item-dropdown .dropdown-list .dropdown-list-item .dropdown-list-link:hover {
  opacity: 1;
}
.page-header .desktop-header .header-navigation .navigation-list .navigation-list-item:hover .list-item-dropdown {
  visibility: visible;
  opacity: 1;
  transform: translate(0px, -10px);
}
.page-header .desktop-header .search-icon-container {
  display: flex;
  flex-direction: row;
  gap: 0px;
  justify-content: flex-end;
  align-items: center;
  width: 5%;
  cursor: pointer;
  margin-bottom: 5px;
}
@media only screen and (max-width: 990px) {
  .page-header .desktop-header .search-icon-container {
    display: none;
  }
}
.page-header .mobile-header-container {
  position: fixed;
  top: 0px;
  right: 0;
  height: var(--doc-height);
  width: 50%;
  background-color: rgba(0, 0, 0, 0.92);
  padding: 25px;
  z-index: 2;
  transition: width 0.3s ease-out;
  display: none;
}
@media only screen and (max-width: 560px) {
  .page-header .mobile-header-container {
    width: 100%;
  }
}
.page-header .mobile-header-container.show {
  display: block;
}
.page-header .mobile-header-container .mobile-header {
  flex-basis: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 50px;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
}
.page-header .mobile-header-container .mobile-header .mobile-menu-close-icon {
  display: block;
  align-self: end;
  color: var(--secondary);
}
.page-header .mobile-header-container .mobile-header .mobile-menu-close-icon .fa-xmark {
  margin-bottom: 5px;
  font-size: 1.3em;
}
.page-header .mobile-header-container .mobile-header .mobile-search {
  display: flex;
  flex-direction: row;
  gap: 15px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-bottom: 1px solid var(--secondary);
  padding: 10px;
}
@media only screen and (max-width: 425px) {
  .page-header .mobile-header-container .mobile-header .mobile-search {
    width: 100%;
  }
}
.page-header .mobile-header-container .mobile-header .mobile-search .mobile-search-input {
  border: none;
  outline: none;
  background-color: var(--primary-bg);
  color: var(--secondary);
  width: 90%;
}
.page-header .mobile-header-container .mobile-header .mobile-search .fa-magnifying-glass {
  color: var(--secondary);
}
.page-header .mobile-header-container .mobile-header .mobile-search:hover .fa-magnifying-glass {
  font-size: 1.2em;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation {
  display: flex;
  flex-direction: column;
  gap: 0px;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 30px;
  justify-content: space-between;
  align-items: center;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item {
  display: flex;
  flex-direction: column;
  gap: 0px;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  width: 100%;
  color: var(--secondary);
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-navigation-item-link {
  font-size: 1.2em;
  padding: 5px;
  cursor: pointer;
  width: 100%;
  text-align: start;
  outline: none;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-navigation-item-link:hover {
  font-weight: var(--semi-bold);
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-dropdown-item-link {
  display: flex;
  flex-direction: row;
  gap: 4px;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-dropdown-item-link .fa-angle-down {
  font-size: 0.8em;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-list-item-dropdown {
  transition: max-height 0.3s ease-out, padding 0.3s ease-out;
  max-height: 0px;
  overflow: hidden;
  width: 100%;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-list-item-dropdown.open {
  padding: 10px;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-list-item-dropdown .mobile-dropdown-list {
  display: flex;
  flex-direction: column;
  gap: 0px;
  justify-content: center;
  align-items: center;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-list-item-dropdown .mobile-dropdown-list .mobile-dropdown-list-item {
  width: 100%;
  padding: 20px 15px;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-list-item-dropdown .mobile-dropdown-list .mobile-dropdown-list-item .mobile-dropdown-list-link {
  text-align: start;
  font-size: 1.1em;
  font-weight: var(--semi-bold);
  opacity: 0.6;
  cursor: pointer;
}
.page-header .mobile-header-container .mobile-header .mobile-header-navigation .mobile-navigation-list .mobile-navigation-list-item .mobile-list-item-dropdown .mobile-dropdown-list .mobile-dropdown-list-item .mobile-dropdown-list-link:hover {
  opacity: 1;
}

.introduction-section {
  width: 100%;
  margin-top: 15%;
}
@media only screen and (min-width: 1600px) {
  .introduction-section {
    margin: 10% auto 0 auto;
  }
}
@media only screen and (max-width: 560px) {
  .introduction-section {
    margin-top: 20%;
  }
}
@media only screen and (max-width: 425px) {
  .introduction-section {
    margin-top: 30%;
  }
}
.introduction-section .introduction-container {
  margin: 80px auto 50px auto;
  display: grid;
  grid-template-columns: 0.7fr 1fr;
  grid-template-rows: unset;
  gap: 20px;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  font-family: var(--lexend);
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container {
    grid-template-columns: unset;
    grid-template-rows: 0.6fr 1.5fr;
    gap: 40px;
    margin: 20px auto 50px auto;
  }
}
@media only screen and (max-width: 425px) {
  .introduction-section .introduction-container {
    grid-template-rows: 0.6fr 1fr;
  }
}
.introduction-section .introduction-container.active .introduction-text-container {
  transform: translateX(0px);
  opacity: 1;
}
.introduction-section .introduction-container.active .introduction-picture-section {
  transform: translateY(0px);
  opacity: 1;
}
.introduction-section .introduction-container .introduction-text-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  padding: 10px;
}
@media only screen and (min-width: 1150px) {
  .introduction-section .introduction-container .introduction-text-container {
    opacity: 0;
    transform: translateX(-150px);
    transition: all 0.6s ease-out;
  }
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-text-container {
    justify-content: flex-start;
    height: unset;
  }
}
.introduction-section .introduction-container .introduction-text-container .introduction-subtitle {
  text-transform: uppercase;
  font-size: 0.6em;
  color: #585858;
  font-weight: var(--middle);
}
.introduction-section .introduction-container .introduction-text-container .introduction-title {
  font-family: omega;
  font-weight: var(--middle);
}
.introduction-section .introduction-container .introduction-text-container .introduction-paragraph {
  font-size: 0.9em;
  line-height: 1.6;
  color: var(--link);
}
.introduction-section .introduction-container .introduction-text-container .introduction-link {
  border-bottom: 1px solid var(--link-border);
}
.introduction-section .introduction-container .introduction-text-container .introduction-link span {
  display: inline-block;
  position: relative;
  font-size: 0.7em;
  text-transform: uppercase;
  cursor: pointer;
}
.introduction-section .introduction-container .introduction-picture-section {
  display: grid;
  grid-template-columns: 1.5fr 0.3fr 0.8fr;
  grid-template-rows: unset;
  gap: 0px;
  justify-content: unset;
  align-items: unset;
  height: 100%;
}
@media only screen and (min-width: 1600px) {
  .introduction-section .introduction-container .introduction-picture-section {
    grid-template-rows: 0.5fr 3fr;
  }
}
@media only screen and (max-width: 1300px) {
  .introduction-section .introduction-container .introduction-picture-section {
    grid-template-columns: 1.5fr 0.7fr 0.8fr;
  }
}
@media only screen and (min-width: 1150px) {
  .introduction-section .introduction-container .introduction-picture-section {
    opacity: 0;
    transform: translateY(150px);
    transition: all 0.6s ease-out;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-photo-container {
  grid-row: 1/3;
  grid-column: 1/3;
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-photo-container {
    grid-column: 1/4;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-photo-container img {
  margin-bottom: -100px;
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-photo-container img {
    margin-bottom: 0px;
    width: 100%;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container {
  grid-row: 2/3;
  grid-column: 2/4;
  padding: 30px 20px;
  border: 5px solid var(--primary);
  display: flex;
  flex-direction: column;
  gap: 35px;
  justify-content: space-between;
  align-items: center;
  font-family: var(--lora);
}
@media only screen and (max-width: 1300px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container {
    gap: 25px;
  }
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container {
    background-color: var(--image-cover);
    color: var(--secondary);
    grid-row: 1/4;
    grid-column: 1/4;
    justify-content: space-between;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-title {
  text-transform: uppercase;
  font-size: 0.6em;
  font-style: italic;
  font-weight: var(--middle);
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text {
  text-align: center;
  font-size: 0.9em;
  line-height: 1.3;
  font-weight: var(--middle);
  color: #3e3e3e;
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text {
    font-size: 1.2em;
    color: var(--secondary);
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text q {
  quotes: "“" "”" "‘" "’";
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text q::before {
  content: open-quote;
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text q::after {
  content: close-quote;
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
  justify-content: center;
  align-items: center;
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .hidden-onion-logo {
  width: 22px;
  height: 22px;
  margin-bottom: 8px;
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .hidden-onion-logo {
    width: 30px;
    height: 30px;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .company-logo-slogan-1, .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .company-logo-slogan-2 {
  text-transform: uppercase;
  font-size: 0.6em;
  font-weight: var(--small);
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .company-logo-slogan-2 {
  font-weight: var(--semi-bold);
}

.representation-section {
  width: 100%;
  margin: 0 auto;
}
.representation-section .representation-container {
  max-width: var(--max-width);
  width: 75%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
  font-family: var(--lora);
  padding: 150px 0px 80px 0px;
}
@media only screen and (max-width: 1300px) {
  .representation-section .representation-container {
    width: 80%;
  }
